import React from 'react';
import ReactDOM from 'react-dom';
import Humanize from 'humanize-plus';


class JonmatListItem extends React.Component {
    constructor(props) {
        super(props);
    }

    render() {
        let item = this.props.item;
        let color = {
            '': '#F9B583',
            '': '#82C9A6',
            '': '#FBEB9D',
            '': '#ACDFF0',
            '': '#F39EB2'
        }[item.kind];

        return (
            <li style={{
                borderColor: color
            }}>
                <div className="inner">
                    <div className="tag" style={{
                        backgroundColor: color
                    }}>{item.kind}</div>
                    <div className="info">
                        <div className="shop">{item.name}</div>
                        <div className="address">{item.address}</div>
                        {/* <div className="who"><span className="name"></span><span> | </span><span></span></div> */}
                        <div className="price">{Humanize.intComma(item.totalPrice)}</div>
                    </div>
                    <div className="count">
                        <div className="num">{item.times}<span className="h"></span></div>
                    </div>
                </div>
            </li>
        )
    }
}


export default class JonmatList extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            items: props.items || []
        }
    }

    componentWillReceiveProps(props) {
        this.setState({
            items: props.items || []
        })
    }

    render() {
        let items = this.state.items.map((item, i) => {
            return <JonmatListItem key={i} item={item} />
        });

        return (
            <div className="jonmat-list">
                <ul>{items}</ul>
            </div>
        )
    }
}



